import { initializeApp } from 'firebase/app';
import { getAuth, GithubAuthProvider, signInWithPopup, signOut } from 'firebase/auth';
import { getFirestore } from 'firebase/firestore';

// Configurao do Firebase do seu projeto
const firebaseConfig = {
  apiKey: "AIzaSyC3eq_SUwf7h4PwR6pcpIiLVDto7IZQHY0",
  authDomain: "lpefirebase-86329.firebaseapp.com",
  projectId: "lpefirebase-86329",
  storageBucket: "lpefirebase-86329.appspot.com",
  messagingSenderId: "570048049090",
  appId: "1:570048049090:web:889400786c49f65612305b"
};

// Inicializar o Firebase
const app = initializeApp(firebaseConfig);
const auth = getAuth(app);
const db = getFirestore(app);

// Configurar o provedor GitHub
const githubProvider = new GithubAuthProvider();

const loginWithGithub = async () => {
  try {
    const result = await signInWithPopup(auth, githubProvider);
    const credential = GithubAuthProvider.credentialFromResult(result);
    const token = credential.accessToken;
    const user = result.user;
    // Armazene as informaes do usurio conforme necessrio
    return { user, token };
  } catch (error) {
    console.error("Erro ao fazer login com GitHub", error);
    throw error;
  }
};

const logout = async () => {
  try {
    await signOut(auth);
  } catch (error) {
    console.error("Erro ao fazer logout", error);
    throw error;
  }
};

export { auth, db, loginWithGithub, logout };
